<template>
    <div>
        <!-- 头部导航 -->
        <van-nav-bar fixed title="账号管理" left-arrow @click-left="$router.back()"
            style="border-bottom: 2px solid #dcdcdc;margin-bottom: 10px;">
            <template #right>
                <van-icon name="ellipsis" size="18" />
            </template>
        </van-nav-bar>
        <!-- 修改内容部分 -->
        <div class="changeInfo">
            <van-cell-group>
                <van-field v-model="nickname" label="昵称" placeholder="请输入昵称" />
                <van-field v-model="signature" label="个性签名" placeholder="请输入个性签名" />
                <van-field v-model="userpwd" label="修改密码" type='password' />
            </van-cell-group>
        </div>
        <!-- 保存、退出登录按钮 -->
        <div class="save">
            <van-button color="rgb(27, 174, 174)" @click="save">保存</van-button>
            <van-button color="rgb(27, 174, 174)" @click="logOutHandle">退出登录</van-button>
        </div>
    </div>
</template>

<script>
// 导入接口
import { loginInfo, saveInfo, logoOut, getIndexInfo } from "@/api/index"
export default {
    name: "account",
    props: [],
    created() {
        // 调用loginInfo接口，获取用户登录信息
        loginInfo().then(data => {
            if (data.resultCode === 200) {
                // console.log(data);
                // 获取用户登录信息实现回显
                this.nickname = data.data.nickName
                this.signature = data.data.introduceSign
            }
        })
    },
    data() {
        return {
            nickname: '',//昵称
            signature: '',//个性签名
            userpwd: '',//密码
        }
    },
    methods: {
        //  点击保存
        save() {
            // 调用保存用户修改信息
            saveInfo(this.signature, this.nickname, this.userpwd).then(data => {
                if (data.resultCode === 200) {
                    // console.log(data);
                    // 提示保存成功
                    this.$toast.success('保存成功');
                }
            })
        },
        // 点击退出登录
        logOutHandle() {
            // 调用退出登录接口
            logoOut().then(data => {
                if (data.resultCode === 200) {
                    // 调用首页数据
                    getIndexInfo().then(data => {
                        if (data.resultCode === 200) {
                            // 返回到首页
                            this.$router.push('/home')
                        }
                    })
                    // this.$router.push('/home')
                    // 将本地服务器的令牌xftoken的值value置为空
                    localStorage.setItem('xftoken', '');
                    // 提示退出成功
                    this.$toast.success('退出成功');
                }
            })
        }
    }
}
</script>

<style lang="less" scoped>
//头部导航左右图标 
/deep/.van-icon {
    color: #252525;
}

.changeInfo {
    width: 94%;
    margin: 50px auto 20px auto;
}

.save {
    margin: 10px auto;
    width: 90%;
    .van-button {
        width: 100%;
        margin: 10px 0px;
        height: 50px;
    }
}
</style>